<template>
	<div class="ratings" ref="ratingView">
		<div class="ratings-wrapper">
			<div class="overview">
				<div class="overview-left">
					<div class="comment-score">
						<p class="score">{{ratings.comment_score}}</p>
						<p class="text">商家評分</p>
					</div>
					<div class="other-score">
						<div class="quality-score item">
							<span class="text">口味</span>
							<Star :score="ratings.quality_score" class="star"></Star>
							<span class="score">{{ratings.quality_score}}</span>
						</div>
						<div class="pack-score item">
							<span class="text">包裝</span>
							<Star :score='ratings.pack_score' class='star'></Star>
							<span class="score">{{ratings.pack_score}}</span>
						</div>
					</div>
				</div>
				<div class="overview-right">
					<div class="delivery-score">	
						<p class="score">{{ratings.delivery_score}}</p>
						<p class="text">配送評分</p>
					</div>
				</div>
			</div>

			<Split></Split>

			<div class="content">	
				<div class="rating-select" v-if="ratings.tab">
					<span class="item" @click="selectTypeFn(2)" :class="{'active' : selectType == 2 }">
						{{ratings.tab[0].comment_score_title}}
					</span>
					<span class="item" @click="selectTypeFn(1)"  :class="{'active' : selectType == 1 }">
						{{ratings.tab[1].comment_score_title}}
					</span>
					<span class="item" @click="selectTypeFn(0)"  :class="{'active' : selectType == 0 }">
						<img src="./icon_sub_tab_dp_normal@2x.png" alt="" v-show="selectType !==0">
						<img src="./icon_sub_tab_dp_highlighted@2x.png" alt="" v-show="selectType == 0">
						{{ratings.tab[2].comment_score_title}}
					</span>
				</div>

				<div class="labels-view">
					<span class="item" v-for="item in ratings.labels" :class="{'highlight' :item.label_star>0}">
						{{item.content}}{{item.label_count}}
					</span>
				</div>

				<ul class="rating-list">
					<li v-for="comment in selectComments" class="comment-item">
						<div class="comment-header">
							<img :src="comment.user_pic_url" v-if="comment.user_pic_url" />
							<img src="./anonymity.png" v-if="!comment.user_pic_url">
						</div>
						<div class="comment-main">
							<div class="user">
								{{comment.user_name}}
							</div>
							<div class="time">
								{{formatDate(comment.comment_time)}}
							</div>
							<div class="star-wrapper">
								<span class="text">評分</span>
								<Star :score='comment.order_comment_score'></Star>
							</div>
							<div class="c_content" v-html="commentStr(comment.comment)"></div>
							<div class="image-wrapper" v-if="comment.comment_pics.length">
								<img v-for="item in comment.comment_pics" :src="item.thumbnail_url"  />
							</div>
						</div>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>

<script>
	//導入Star組件
	import Star from '../Star/Star.vue'

	//導入Split組件
	import Split from '../Split/Split.vue'

	// 導入BScroll
	import BScroll from 'better-scroll'

	const ALL = 2; // 全部
	const PICTURE = 1; // 帶圖片
	const COMMENT = 0; //點評

	export default {
		data(){
			return {
				ratings: {},
				selectType: ALL
			}
		},
		created(){

			let that = this;

			this.$axios.get('/api/ratings')
		    // 通過axios發起get請求
		    .then(function (response) {

	    	var dataSource = response.data;

	    	if(dataSource.code == 0){
	    		// console.log(dataSource.data.comment_num)
	    		that.ratings = dataSource.data;

	    		//初始化滾動
	    		that.$nextTick(() =>{
	    			if(!that.scroll){
	    				that.scroll = new BScroll(that.$refs.ratingView,{
	    					click: true
	    				});
	    			}else{
	    				that.scroll.refresh();
	    			}
	    		});
	    	}
	    })
	    .catch(function (error) { //error處理
	    	console.log(error);
	    });
	},
	methods: {
		selectTypeFn(type){
			this.selectType = type;

			//刷新操作
			this.$nextTick(()=>{
				this.scroll.refresh();
			})
		},
		formatDate(time){
			let date = new Date(time*1000)

			//時間格式

			let fnt = 'yyyy.MM.dd'

			if(/(y+)/.test(fnt)){ //正規表達示: -> /(y+)/ -> +功能為匹配一個或多個y,()功用為先儲存起來
				let year = date.getFullYear().toString();
				fnt = fnt.replace(RegExp.$1,year); //replace用法:http://www.w3school.com.cn/js/jsref_replace.asp,將所有y取代為fnt
			}
			if(/(M+)/.test(fnt)){
				let month = date.getMonth() + 1; //取的值為0-11
				if(month<10){
					month = '0' + month;
				}
				fnt = fnt.replace(RegExp.$1,month);
			}
			if(/(d+)/.test(fnt)){
				let mydate = date.getDate();
				if(mydate<10){
					mydate = '0' + mydate;
				}
				fnt = fnt.replace(RegExp.$1,mydate);
			}

			return fnt;
		},
		commentStr(content){
			let rel = /#[^#]+#/g; //#號中間不能出現#號

			return content.replace(rel, '<i style="color:#576b95;">$&</i>');
		}
	},
	computed: {
		selectComments(){
			if(this.selectType == ALL){//全部
				return this.ratings.comments;
			} else if(this.selectType == PICTURE){ //有圖
				let arr = [];
				this.ratings.comments.forEach((comment)=>{
					if(comment.comment_pics.length){
						arr.push(comment);
					}
				});
				return arr;
			} else { //點評
				return this.ratings.comments_dp.comments;
			}
		}
	},
	components: {
		Star,
		Split,
		BScroll
	}
}
</script>

<style lang="css" scoped>
	@import url(Ratings.css);
</style>


