<template>
	<div class="header">
		
		<!---頂部-->
		<div class="top-wrapper">
			
			<div class="back">
				<span class="icon-arrow_lift"></span>
			</div>

			<form class="search">
				<span class="search-icon"></span>
				<input type="text" class="search-bar" placeholder="搜索">
			</form>

			<div class="more">
				<a href="#" class="spelling-bt">拚單</a>
				<div class="more-bt">
					<i class="s-radius"></i>
					<i class="s-radius"></i>
					<i class="s-radius"></i>
				</div>
			</div>

		</div>

		<!--主題內容-->
		<div class="content-wrapper">
			<div class="icon" :style="head_bg">
				<!-- <img :src="poiInfo.pic_url" alt=""> -->
			</div>
			<div class="name">
				<h3>{{poiInfo.name}}</h3>
			</div>
			<div class="collect">
				<img src="../../../resource/resource/img/star.png" alt="">
				<span>收藏</span>
			</div>
		</div>

		<!--公告內容-->
		<div class="bulletin-wrapper">
			<img class="icon" v-if="poiInfo.discounts2" :src="poiInfo.discounts2[0].icon_url">

			<span class="text" v-if="poiInfo.discounts2">{{poiInfo.discounts2[0].info}}</span>

			<div class="detail" v-if="poiInfo.discounts2" @click="ShowBulletin">
				{{poiInfo.discounts2.legnth}}個活動
				<span class="icon-keyboard_arrow_right"></span>
			</div>
		</div>

		<!-- 公告詳情 -->
		<transition name="detail">
			<div class="bulletin-detail" v-show="isShow">
				<div class="detail-wrapper" >
					<div class="main-wrapper" :style="detail_bg">
						<div class="icon" :style="head_bg"></div>
						<h3 class="name">{{poiInfo.name}}</h3>
						<!-- 評價 -->
						<div class="score">
							<Star :score="poiInfo.wm_poi_score"></Star>
							<span>{{poiInfo.wm_poi_score}}</span>
						</div>
						<p class="tip">
							{{poiInfo.min_price_tip}} <i>|</i>
							{{poiInfo.shipping_fee_tip}} <i>|</i>
							{{poiInfo.delivery_time_tip}} 
						</p>
						<p class="time">
							配送時間:
							{{poiInfo.shipping_time}}
						</p>
						<div class="discounts" v-if="poiInfo.discounts2">
							<p>
								<img :src="poiInfo.discounts2[0].icon_url">
								<span>{{poiInfo.discounts2[0].info}}</span>
							</p>
						</div>
					</div>

					<div class="close-wrapper" @click="CloseBulletin">
						<span class="icon-close"></span>
					</div>
				</div>
			</div>
		</transition>
		
		<!--背景-->
		<div class="bg-wrapper" :style="content_bg">
			<!-- <img :src="poiInfo.head_pic_url" alt=""> -->
		</div>

	</div>
</template>

<script>

	import Star from '../Star/Star.vue';

	export default{
		data(){
			return {
				isShow: false// 公告詳情預設不顯示
			}
		},
		props: {
			poiInfo:{ //組件傳值
				type:Object,
				default:{}
			}
		},
		components: {
			Star
		},
		computed: {
			content_bg(){
				return "background-image: url(" + this.poiInfo.head_pic_url + ");"
			},
			head_bg(){
				return "background-image: url(" + this.poiInfo.pic_url + ");"
			},
			detail_bg(){
				return "background-image: url(" + this.poiInfo.poi_back_pic_url + ");"
			}
		},
		methods:{
			ShowBulletin(){
				this.isShow = true;
			},
			CloseBulletin(){
				this.isShow = false;
			}
		}
	}
</script>

<style>
	/*導入css*/
	@import url(Header.css);
</style>


