<template>
	<div class="goods">
		<!-- 分類 -->
		<div class="menu-wrapper" ref="menuScroll">
      <ul>
        <!-- 專場 -->
        <li class="menu-item"  :class="{'current':currentIndex===0}"  @click="selectMenu(0)">
          <p class="text">
            <img :src="container.tag_icon" v-if="container.tag_icon" class="icon">
            {{container.tag_name}}
          </p>
        </li>

        <li class="menu-item" v-for="(item,index) in goods" :class="{'current':currentIndex===index+1}"  @click="selectMenu(index+1)">
          <p class="text">
            <img :src="item.icon" v-if="item.icon" class="icon">
            {{item.name}}
          </p>

          <i class="num" v-show="calculateCount(item.spus)">{{calculateCount(item.spus)}}</i>
        </li>
      </ul>  
    </div>

    <!-- 商品列表 -->
    <div class="foods-wrapper" ref="foodScroll">
      <ul>

        <!-- 專場 -->
        <li class="container-list food-list-hook">
          <div v-for="item in container.operation_source_list">
            <img :src="item.pic_url">
          </div>
        </li>

        <!-- 具體分類 -->
        <li v-for="item in goods" class="food-list food-list-hook" >

          <h3 class="title">{{item.name}}</h3>
          <!-- 具體商品列表 -->
          <ul>
            <li v-for="food in item.spus" class="food-item" @click="showDetail(food)">
              <div class="icon" :style="head_bg(food.picture)" ></div>

              <div class="content">
                <h3 class="name">{{food.name}}</h3>
                <p class="desc" v-if="food.description">{{food.description}}</p>
                <div class="extra">
                  <span class="saled">{{food.month_saled_content}}</span>
                  <span class="praise">{{food.praise_content}}</span>
                </div>
                <img class="product" :src="food.product_label_picture">
                <p class="price">
                  <span class="text">${{food.min_price}} / </span>
                  <span class="unit">{{food.unit}}</span>
                </p>
              </div>
              
              <div class="cartcontrol-wrapper">
                <Cartcontrol :food='food'></Cartcontrol>
              </div>

            </li>
          </ul>

        </li>
        

      </ul>
    </div>
    
    <!-- 購物車 -->
    <Shopcart :shipping_fee_tip='poiInfo.shipping_fee_tip' :min_price_tip='poiInfo.min_price_tip' :selectFoods="selectFoods" :poiInfo="poiInfo"></Shopcart>

    <!-- 商品詳情 -->
    <Food :food="selectedFood" ref="foodView"></Food>
  </div>
</template>

<script>
// 導入BScroll
import BScroll from 'better-scroll'

//導入Shopcart
import Shopcart from '../Shopcart/Shopcart.vue'

// 導入Cartcontrol
import Cartcontrol from '../Cartcontrol/Cartcontrol.vue'

// 導入Food
import Food from '../Food/Food.vue'

export default {
 data(){
  return{
   container: {},
   goods: [],
   poiInfo:[],
   listHeight: [],
   scrollY: 0,
   menuScroll: {},
   foodScroll: {},
   selectedFood: {}
 }
},

  created(){ //發起請求，獲取數據。

    var that = this;

    // 向具有指定ID的用户发出请求
    this.$axios.get('/api/Goods')
    // 通過axios發起get請求
    .then(function (response) {//獲取到數據
      //console.log(response);
      //console.log('------');
      var dataSource = response.data;
      //console.log(dataSource);
      // console.log('------');
      if(dataSource.code == 0){
        that.container = dataSource.data.container_operation_source;
        that.goods = dataSource.data.food_spu_tags;
        that.poiInfo = dataSource.data.poi_info;
        // console.log(that.container);
        // console.log(that.goods);

        //調用滾動的初始化方法
        //that.initScroll();
        //開始時,DOM元素沒有渲染,即高度是問題
        //在獲取到數據後,並DOM已經被渲染，表示列表高度是沒問題

        //nextTick

        that.$nextTick( ()=>{
          //DOM已經更新
          that.initScroll();

          // 計算分類區間高度
          that.calculateHeight();
        });
      }
    })
    .catch(function (error) { //error處理
      console.log(error);
    });
  },
  computed: { //計算屬性(不能傳遞參數),computed依賴data裡的變量改變了,computed會自動重新計算
    currentIndex(){// 根據右側滾動位置,確定對應的索引下標
      for (let i = 0; i < this.listHeight.length; i++){
        let height1 = this.listHeight[i];
        let height2 = this.listHeight[i + 1];
        // console.log(this.listHeight.length);
        // console.log(i);
          //是否在上述區間中
          if(!height2 ||(this.scrollY >= height1 && this.scrollY < height2)){
            return i;
          }
        }

        return 0;
      },
      selectFoods(){
        let foods = [];

        this.goods.forEach( (good)=>{
          good.spus.forEach( (d)=>{
            if(d.count>0){
              foods.push(d);
            }
          });
        });
        return foods;
      }
    },
    methods: {
      head_bg(img){
        return "background-image: url(" + img + ");"
      },
    //滾動初始化
    initScroll(){
      // ref屬性是用來綁訂某個DOM元素,或某個組件,然後在this.$refs可找到
      this.menuScroll = new BScroll(this.$refs.menuScroll,{
        click: true
      });
      this.foodScroll = new BScroll(this.$refs.foodScroll,{
        click: true,
        probeType: 3
      });

      //添加滾動監聽事件
      this.foodScroll.on('scroll',(pos) => { // => JS ES6語法,詳情: http://bit.ly/2HlQ5Qz 
        // console.log(pos.y);
          this.scrollY = Math.abs(Math.round(pos.y)); //Math.abs取絕對值,Math.round取四捨五入近似值
        });
    },
    calculateHeight(){
      //通過$refs獲取到對應元素
      let foodlist = this.$refs.foodScroll.getElementsByClassName('food-list-hook');

      //添加到陣列區間
      // 0-220.66 第一區間(專場)
      //221-1221  第二區間(熱銷)

      let height = 0;
      this.listHeight.push(height);

      for(let i = 0 ; i < foodlist.length; i++) {
        let item = foodlist[i];

        //累加
        height += item.clientHeight;

        this.listHeight.push(height);

        // console.log(this.listHeight);

      }
    },

    selectMenu(index){
      // console.log(index);

      let foodlist = this.$refs.foodScroll.getElementsByClassName('food-list-hook');

       //根據下標,滾動到相對應的元素
       let el = foodlist[index];

       // 滾動到相對應的位置

       this.foodScroll.scrollToElement(el,500);
     },
    calculateCount(spus){
      let count = 0;
      spus.forEach((food)=>{
        if(food.count>0){
          count += food.count;
        }
      });
      return count;
    },
    showDetail(food) {
      //傳值
      this.selectedFood = food;

      // 顯示詳情頁
      this.$refs.foodView.showView();
    }

   },
   components: {
    BScroll,
    Shopcart,
    Cartcontrol,
    Food
  }
}
</script>

<style lang="css" scoped>
@import url("Goods.css");
</style>


