<template>
	<div class="nav">

		<router-link class='nav-item'to="/Goods">
			點菜

			<i class="line"></i>
		</router-link>

		<router-link class='nav-item'to="/Ratings">
			評價({{commentNum}})
			<i class="line"></i>
		</router-link>

		<router-link class='nav-item'to="/Seller">
			商家
			<i class="line"></i>
		</router-link>

	</div>

</template>

<script>
	export	default{
		props: {
			commentNum: {
				type: Number,
				default: 0
			}
		}
	}
</script>

<style>
	.nav{
		display: flex;
		width:  100%;
		height: 40px;
		line-height: 40px;
		border-bottom: 1px solid #e4e4e4;
	}

	.nav-item {
		position: relative;
		flex: 1;
		text-align: center;
		font-size: 13px;
		text-decoration: none;
		color: #666666;
	}

	/*選重後的css*/

	.nav .active{
		color:  #FFBB22;
	}

	.nav .active .line{
		width: 21px;
		height: 2px;
		display: inline-block;
		background: #FFBB22;
		position: absolute;
		left: 50%;
		bottom: 0;
		transform: translateX(-50%);
	}

</style>


