<template>
	<div class="shopcart" :class="{'highlight' :totalCount>0}">
		
		<div class="shopcart-wrapeer">
			
			<div class="content-left">
				<div class="logo-wrapper" :class="{'highlight' :totalCount>0}">
					<span class="icon-shopping_cart" :class="{'highlight' :totalCount>0}"  @click="toggleList"></span>
					<i class="num" v-show="totalCount">{{totalCount}}</i>
				</div>
				<div class="desc-wrapper">
					<p class="totalprice" v-show="totalPrice">${{totalPrice}}</p>
					<p class="tip" :class="{'highlight' :totalCount>0}">另需{{poiInfo.shipping_fee_tip}}</p>
				</div>
			</div>

			<div class="content-right" :class="{'highlight' :totalCount>0}">
				{{payStr}}
			</div>

			<div class="shopcart-list" v-show="listShow" :class="{'show':listShow}">
				<div class="list-top" v-if="poiInfo.discounts2">
					{{poiInfo.discounts2[0].info}}
				</div>
				<div class="list-header">
					<h3 class="title">1號口袋</h3>
					<div class="empty" @click="emptyfn">
						<img src="./ash1_bin.png" alt="">
						<span>清空購物車</span>
					</div>
				</div>
				<div class="list-content" ref="listContent">
					<ul>
						<li class="food-item" v-for="food in selectFoods">
							<div class="desc-wrapper">
								<div class="desc-left">
									<p class="name">{{food.name}}</p>
									<p class="unit" v-show="!food.description">{{food.unit}}</p>
									<p class="description"  v-show="food.description">{{food.description}}</p>
								</div>
								<div class="desc-right">
									<span class="price">${{food.min_price}}</span>
								</div>
							</div>
							<div class="cartcontrol-wrapper">
								<Cartcontrol :food="food"></Cartcontrol>
							</div>
						</li>
					</ul>
				</div>
				<div class="list-bottom"></div>
			</div>
		</div>

		<div class="list-mask" v-show="listShow" @click="hidemask"></div>
	</div>

</template>

<script>

import Cartcontrol from '../Cartcontrol/Cartcontrol.vue'

import BScroll from 'better-scroll'

export default {
	data(){
		return{
			fold: true
		}
	},
	props: {
			// min_price_tip:{
			// 	type: String,
			// 	default: ''
			// },
			// shipping_fee_tip:{
			// 	type: String,
			// 	default: ''
			// },
			poiInfo: {
				type: Object,
				default: {}
			},
			selectFoods: {
				type: Array,
				default(){
					// return[
					// 	// {
					// 	// 	min_price: 10,
					// 	// 	count: 7
					// 	// },

					// 	// {
					// 	// 	min_price: 7,
					// 	// 	count: 7
					// 	// }
					// ];
				}
			}
		},
		computed: {
				// 總個數
				totalCount() {
					let num = 0;
					this.selectFoods.forEach( (food) => {
						num += food.count;
					} );
					return num;
				},
				//總金額
				totalPrice(){
					let total = 0;
					this.selectFoods.forEach( (food) => {
						total += food.min_price * food.count;
					} );

					return total;
				},
				//結算按鈕顯示
				payStr() {
					if(this.totalCount > 0){
						return "去結算";
					} else {
						{
							return this.poiInfo.min_price_tip;
						}
					}
				},
				listShow(){
					if(!this.totalCount){ //個數為0
						this.fold = true;

						return false;
					}

					let show = !this.fold; //預設fold(摺疊)true,點擊logo觸發toggleList將fold設為false,即可打開購物籃清單


					// 滾動操作(BScroll)
					if(show){
						this.$nextTick(() =>{
							if(!this.shopScroll){
								this.shopScroll = new BScroll(this.$refs.listContent,{
									click: true
								});
							} else {
								this.shopScroll.refresh();
							}
						});
					}
					// console.log(show)
					return show;

				}
			},
			methods: {
				toggleList(){
					if(!this.totalCount){ //個數為0
						return;
					}

					this.fold = !this.fold; 

				},
				emptyfn(){
					this.selectFoods.forEach((food) =>{
						food.count = 0;
					});
				},
				hidemask(){
					this.fold = true;
				},
			},
			components: {
				Cartcontrol,
				BScroll
			}
		}
		</script>

		<style lang="css" scoped>
		@import url(Shopcart.css);
		</style>


